.logo {
  color: white;
  font-weight: 600;
  text-align: center;
  font-size: 11vmin;
  margin-bottom: 0;
  margin-top: 5vmin;
}

@media only screen and (max-height: 600px) {
  .logo {
    margin-top: 0.2em;
  }
}

@media only screen and (max-height: 700px) {
  .logo {
    margin-top: 0.3em;
  }
}
