.task-card {
  background-color: hsl(var(--hue), 50%, 50%);
  border-radius: 1vmin;
  position: absolute;
  aspect-ratio: 5 / 7;
  width: 25vmin;
  margin: 0 auto;
  transition: all 800ms cubic-bezier(0.09, 0.72, 0.33, 0.84);
  cursor: pointer;
  padding-top: 1em;
  display: flex;
  justify-content: center;
  box-shadow: -1px 1px 20px rgb(0, 0, 0);
  z-index: 1;
}

/* necessary for buttons to follow the stagger and size of card
 */
.task-card-container-copy {
  border-radius: 1vmin;
  position: absolute;
  aspect-ratio: 5 / 7;
  width: 25vmin;
  margin: 0 auto;
  padding-top: 1em;
  transition: all 800ms cubic-bezier(0.09, 0.72, 0.33, 0.84);
}

/* if task is completed */
.completed {
  background-color: rgb(169, 169, 169);
  text-decoration: line-through;
}

.task-text {
  width: 70%;
  word-wrap: break-word;
  margin: 1em 1em;
  overflow: scroll;
  font-size: 2vmin;
}

.task-text-spread {
  width: 80%;
  word-wrap: break-word;
  margin: 0em 1em 1em 1em;
  overflow: scroll;
  font-size: 1vmin;
}

.selected {
  background-color: hsl(var(--hue), 50%, 35%);
  -webkit-transition: none !important;
  -moz-transition: none !important;
  -o-transition: none !important;
  transition: none !important;
  z-index: 2;
}

/* Task spread styling */

.task-card-size-spread {
  width: 15.25vmin !important;
  top: -40%;
}

.each-card:nth-last-child(2) .spread-task {
  transform: translate(0%, -9%) rotate(0deg) !important;
}

.each-card:nth-last-child(3) .spread-task {
  transform: translate(-60%, 2%) rotate(-27.69deg) !important;
}

