/* card suit styling */

.card-suit-top {
  position: absolute;
  top: 4%;
  left: 3%;
  color: black;
}

.card-suit-bottom {
  position: absolute;
  top: 79%;
  right: 3%;
  color: black;
  rotate: 180deg;
}

.card-suit-icon {
  font-size: 2vmin;
}

.card-suit-value {
  margin: 0;
  padding: 0;
  font-size: 3vmin;
  font-weight: normal;
}

/* in spread mode */
.card-suit-top-spread {
  position: absolute;
  top: 4%;
  left: 3%;
  color: black;
}

.card-suit-bottom-spread {
  position: absolute;
  top: 79%;
  right: 3%;
  color: black;
  rotate: 180deg;
}

.card-suit-icon-spread {
  font-size: 1.22vmin;
}

.card-suit-value-spread {
  margin: 0;
  padding: 0;
  font-size: 1.83vmin;
  font-weight: normal;
}
